.PaymentHistory {
  font-family: Roboto;
}

.PaymentHistory_Main {
  width: 75%;
  margin: 5% auto;
}

.Detail {
  display: grid;
  padding: 2% 0 20px 0;
  margin-left: 3%;
  list-style: none;
  position: relative;
  grid-template-columns: 60% 40%;
}

.Payment_Grid_Detail h1 {
  font-size: 16px;
  font-weight: 500;
  color: #24272c;
}

.Payment_Grid_Detail p {
  font-size: 12px;
  color: #566171;
  margin-top: 1.12%;
}
.Payment_Grid_Amount h1 {
  font-size: 14px;
  font-weight: 500;
  text-align: right;
  color: #24272c;
}

.Invoice_Btn {
  height: 35px !important;
  color: black !important;
  background: white !important;
  padding: 0 10% 0 10% !important;
  border-radius: 4px !important;
  border: solid 1px rgba(36, 39, 44, 0.15) !important;
  font-size: 13px !important;
  font-weight: 500 !important;
  color: #24272c !important;
  margin-left: 0 !important;
  margin-top: 4% !important;
  box-shadow: none !important;
}
